@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Proxima Nova";
  font-display: swap;
  src: url("../fonts/proximanovaregular.woff2") format("woff2"), url("../fonts/proximanovaregular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Proxima Nova";
  font-display: swap;
  src: url("../fonts/proximanovabold.woff2") format("woff2"), url("../fonts/proximanovabold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.button {
  display: block;
  font-weight: 700;
  font-size: 18px;
  line-height: normal;
  font-family: "Proxima Nova", "Arial", sans-serif;
  color: #ffffff;
  text-transform: capitalize;
  text-decoration: none;
  background-color: #48b774;
  border: none;
  cursor: pointer;
}
.button--purple {
  background: linear-gradient(265deg, #4870FF -5.06%, #FA00FF 94.87%);
  box-shadow: 0 4px 16px 0 rgba(138, 97, 255, 0.6);
}
.button--transparent {
  background-color: transparent;
}
.button:hover {
  opacity: 0.9;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  opacity: 0.7;
}
.button:focus-visible {
  outline: 3px #48b774 solid;
  outline-offset: 2px;
}
.button:disabled {
  color: #ffffff;
  background-color: #e5e5e5;
  cursor: default;
}

.page {
  height: 100%;
}

.page-body {
  height: 100%;
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  font-family: "Proxima Nova", "Arial", sans-serif;
  color: #ffffff;
  background-color: #f2f2f2;
}

.content-wrapper {
  display: none;
  flex-direction: column;
  width: 100%;
  min-width: 320px;
  max-width: 1440px;
  min-height: 100%;
  margin: 0 auto;
  background-color: #ecf7f3;
  box-shadow: 0 5px 15px rgba(0, 1, 1, 0.2);
}

.main-header {
  display: flex;
  padding: 12px;
  background: rgba(1, 1, 31, 0.96);
}
.main-header.no-js {
  padding: 0;
}
@media (min-width: 768px) {
  .main-header {
    backdrop-filter: blur(5px);
    padding: 12px 60px;
  }
}
@media (min-width: 1200px) {
  .main-header {
    padding: 12px 200px;
  }
}

.main-header__navigation {
  display: flex;
}

.main-header__logo {
  display: flex;
  flex-shrink: 0;
  margin: auto auto auto 0;
}
@media (min-width: 768px) {
  .main-header__logo {
    margin-right: 64px;
  }
}
.main-header__logo:hover {
  opacity: 0.8;
}
.main-header__logo:active {
  opacity: 0.5;
}
.main-header__logo:focus-visible {
  outline: none;
  opacity: 0.8;
}

.main-header__toggle {
  display: flex;
  flex-direction: column;
  align-self: center;
  margin-left: auto;
}
@media (min-width: 768px) {
  .main-header__toggle {
    display: none;
  }
}
.main-header__toggle:hover {
  opacity: 0.6;
}
.main-header__toggle:active {
  opacity: 0.3;
}
.main-header__toggle:focus-visible {
  outline: 3px #766357 solid;
  outline-offset: 2px;
}
.main-header__toggle:disabled {
  opacity: 0.2;
}

.main-header__button {
  margin: auto 0;
  padding: 9px 32px;
  border-radius: 8px;
}

.main-header__button-text {
  display: block;
  width: 100%;
  max-width: 160px;
}

.main-container {
  flex-grow: 1;
}

@media (min-width: 768px) {
  .navigation {
    margin-right: auto;
  }
}

.navigation__list {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  display: none;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #81b3d2;
  transform: translateY(-100%);
  transition: transform 0.3s ease;
  gap: 1px;
}
@media (min-width: 768px) {
  .navigation__list {
    position: relative;
    z-index: 0;
    display: flex;
    flex-wrap: wrap;
    align-self: center;
    background-color: transparent;
    transform: translateY(0);
    transition: none;
    gap: 40px;
  }
}
.is-open .navigation__list {
  display: grid;
  min-width: 320px;
  transform: translateY(0);
}
@media (min-width: 768px) {
  .is-open .navigation__list {
    display: flex;
  }
}
.no-js .navigation__list {
  position: relative;
  display: grid;
  transform: translateY(0);
  transition: none;
}

.navigation__item {
  box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
@media (min-width: 768px) {
  .navigation__item {
    display: flex;
    justify-content: center;
    box-shadow: none;
  }
}
@media (max-width: 767px) {
  .navigation__item:last-child {
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 90%, rgba(0, 0, 0, 0.2) 100%);
  }
}
@media (min-width: 768px) {
  .navigation__item:nth-child(4n-1) {
    grid-column-start: 4;
  }
}

.navigation__link {
  position: relative;
  display: block;
  padding: 17px 67px 12px 67px;
  font-weight: 700;
  font-size: 18px;
  line-height: normal;
  text-align: center;
  color: #ffffff;
  text-transform: capitalize;
  text-decoration: none;
}
@media (min-width: 768px) {
  .navigation__link {
    padding: 0;
    max-width: 250px;
    font-size: 16px;
    background-color: transparent;
  }
}
.navigation__link--current {
  background: linear-gradient(265deg, #4870FF -5.06%, #FA00FF 94.87%);
  background-clip: text;
  color: transparent;
}
@media (min-width: 768px) {
  .navigation__link--current::after {
    content: "";
    position: absolute;
    bottom: -24px;
    left: 50%;
    width: 50px;
    height: 4px;
    border-radius: 4px 4px 0 0;
    background: linear-gradient(265deg, #4870FF -5.06%, #FA00FF 94.87%);
    transform: translate(-50%);
  }
}
.navigation__link:hover {
  background-color: #669ec0;
}
@media (min-width: 768px) {
  .navigation__link:hover {
    color: #81b3d2;
    background-color: #ecf7f3;
  }
}
.navigation__link:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496bd;
}
@media (min-width: 768px) {
  .navigation__link:active {
    color: rgba(0, 0, 0, 0.3);
    background-color: #ecf7f3;
  }
}
.navigation__link:focus-visible {
  outline: 3px #766357 solid;
}
@media (min-width: 768px) {
  .navigation__link:focus-visible {
    color: #81b3d2;
    background-color: #f2f2f2;
    outline: none;
  }
}

.hero {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 90px 19px 85px 19px;
  text-align: center;
  background-color: #000000;
  background-image: url("../img/background/bg_hero.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
@media (min-width: 768px) {
  .hero {
    padding: 50px 61px 0 40px;
  }
}
@media (min-width: 1200px) {
  .hero {
    padding: 54px 288px 54px 288px;
  }
}

.hero__text {
  max-width: 686px;
  margin: 0;
  margin-right: auto;
  margin-bottom: 6px;
  margin-left: auto;
  padding-top: 0;
  font-weight: 700;
  font-size: 48px;
  line-height: normal;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
}

.hero__description {
  max-width: 400px;
  margin: 0;
  margin-right: auto;
  margin-bottom: 30px;
  margin-left: auto;
  font-weight: 400;
  font-size: 28px;
  line-height: normal;
  color: #ffffff;
  text-transform: capitalize;
}

.hero__button-wrapper {
  display: flex;
  justify-content: center;
  margin-bottom: 59px;
}

.hero__button {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 160px;
  margin: 0;
  padding: 11px 23px 11px 24px;
  text-align: center;
  border-radius: 8px;
}
.hero__button:first-child {
  margin-right: 16px;
  background-color: transparent;
  border: 2px solid #ffffff;
}
@media (min-width: 1200px) {
  .hero__button {
    margin-left: 0;
  }
}

.hero__account-button-text {
  display: block;
  width: 100%;
}

.hero__account {
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  max-width: 864px;
  padding: 9px 16px 20px 16px;
  background: linear-gradient(268deg, rgba(79, 89, 124, 0.4) 0%, rgba(123, 135, 175, 0.4) 50.52%, rgba(63, 72, 105, 0.4) 100%);
  border: 1px solid #3a4054;
  border-radius: 16px;
  backdrop-filter: blur(5px);
}

.hero__account-text {
  margin: 0;
  margin-right: auto;
  padding: 0;
  font-weight: 700;
  font-size: 22px;
  line-height: normal;
  text-transform: uppercase;
}

.hero__account-list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}

.hero__account-item {
  position: relative;
  max-width: 117px;
  padding-left: 42px;
  font-weight: 600;
  font-size: 12px;
  line-height: normal;
  text-transform: capitalize;
}
.hero__account-item:first-child::before {
  background-image: url("../img/svg-image/dice.png");
}
.hero__account-item:nth-child(2)::before {
  background-image: url("../img/svg-image/slot.png");
}
.hero__account-item:nth-child(3)::before {
  background-image: url("../img/svg-image/24h.png");
}
.hero__account-item::before {
  position: absolute;
  content: "";
  background-repeat: no-repeat;
  background-size: 36px 36px;
  width: 75px;
  height: 75px;
  left: 0;
  top: -3px;
}

.hero__account-form {
  position: relative;
  padding-top: 15px;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 100%;
  margin-right: auto;
  margin-left: auto;
  gap: 16px;
}
@media (min-width: 1200px) {
  .hero__account-form {
    margin-right: 0;
    margin-left: 0;
  }
}

.hero__account-input {
  width: 100%;
  max-width: 207px;
  padding: 13px 14px 12px 14px;
  font-weight: 400;
  font-size: 16px;
  line-height: normal;
  font-family: "Proxima Nova", "Arial", sans-serif;
  letter-spacing: 0.16px;
  background-color: #ffffff;
  border: none;
  border-radius: 8px;
  box-shadow: 0 20px 30px 0 rgba(219, 219, 219, 0.25);
}

.hero__account-input::placeholder {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: normal;
  color: #050d2a;
  text-transform: capitalize;
  opacity: 0.4;
}

.hero__account-checkbox-link {
  font-weight: 400;
  font-size: 14px;
  line-height: normal;
  color: #ffffff;
  text-transform: capitalize;
  text-decoration-line: underline;
}

.burger-button {
  position: relative;
  z-index: 3;
  order: 3;
  width: 23px;
  height: 22px;
  padding: 0;
  background-color: inherit;
  border: none;
  cursor: pointer;
  transition: transform 0.3s ease;
}
.no-js .burger-button {
  display: none;
}

.burger-button__line {
  display: block;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
  transition: all 0.3s ease;
}
.burger-button__line:not(:last-child) {
  margin-bottom: 5px;
}

.is-open .burger-button__line {
  width: 27px;
  background-color: #ffffff;
}
.is-open .burger-button__line:nth-child(2) {
  position: relative;
  top: 48%;
  transform: rotate(45deg);
}
.is-open .burger-button__line:nth-child(3) {
  display: none;
}
.is-open .burger-button__line:nth-child(4) {
  position: relative;
  top: 10%;
  transform: rotate(-45deg);
}

.games {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin: 0;
  padding: 0 50px 8px 50px;
  background-image: url("../img/background/bg_games.png");
  background-color: rgba(0, 0, 0, 0.93);
  background-position: bottom;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 1250px) {
  .games {
    padding: 0 200px 8px 200px;
  }
}

.games__header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.games__title {
  margin: 0;
  margin-bottom: 2px;
  padding: 0;
  font-weight: 700;
  font-size: 36px;
  line-height: normal;
  text-transform: capitalize;
}

.games__item-title-wrapper {
  margin-right: auto;
}

.games__description {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: normal;
}

.games__link {
  align-self: end;
  font-weight: 600;
  font-size: 16px;
  line-height: normal;
  color: #ffffff;
  text-decoration: none;
  text-decoration-line: underline;
}

.games__list {
  display: grid;
  margin: 0;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 24px;
  padding: 0;
  padding-top: 24px;
  list-style: none;
  gap: 16px;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto;
}
@media (min-width: 1250px) {
  .games__list {
    grid-template-columns: 160px 160px 160px 248px 248px;
    grid-template-rows: repeat(2, auto);
  }
}

.games__item {
  position: relative;
  margin: 0;
  padding: 0;
}
.games__item--big {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  grid-column: 1/-1;
}
@media (min-width: 1250px) {
  .games__item--big {
    grid-column: 4/-1;
  }
}

.games__item-image {
  display: block;
  min-height: 120px;
  max-height: 120px;
  margin-bottom: 6px;
  border-radius: 16px;
  box-shadow: 0 0 24px -4px rgba(72, 112, 255, 0.2);
  object-fit: cover;
}
.games__item-image--inside {
  height: 160px;
  max-height: 160px;
}
@media (max-width: 1250px) {
  .games__item-image {
    width: 100%;
  }
}

.games__item-link {
  color: #ffffff;
  text-decoration: none;
}
.games__item-link:hover {
  opacity: 0.8;
}
.games__item-link:active {
  opacity: 0.6;
}

.games__item-title {
  margin: 0;
  padding: 0;
  font-weight: 700;
  font-size: 16px;
  line-height: normal;
}
.games__item-title--inside {
  position: absolute;
  top: 16px;
  left: 12px;
}
.games__item-title--current {
  color: #4870ff;
}

.games__item-description {
  margin: 0;
  padding: 0;
  font-weight: 300;
  font-size: 12px;
  line-height: normal;
  opacity: 0.5;
}
.games__item-description--inside {
  position: absolute;
  top: 59px;
  left: 12px;
  max-width: 162px;
  font-weight: 400;
  font-size: 10px;
  opacity: 1;
}

.games__item-button {
  align-self: start;
  padding: 5px 16px;
  font-style: normal;
  font-weight: 600;
  font-size: 10px;
  line-height: normal;
  background: #4870ff;
  border: 1px solid #000000;
  border-radius: 17px;
}
.games__item-button--inside {
  position: absolute;
  bottom: 16px;
  left: 12px;
  padding: 5px 13px 5px 14px;
  border: none;
}

.games__attention {
  margin: 0;
  padding: 0;
  margin-bottom: 48px;
  max-width: 655px;
}

.contact {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  padding-top: 26px;
}

.contact__wrapper {
  margin: 0 auto;
}
@media (min-width: 1200px) {
  .contact__wrapper {
    margin: 0;
  }
}

.contact__title {
  margin: 0;
  padding: 0;
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: normal;
  text-transform: capitalize;
}

.contact__description {
  margin: 0;
  padding: 0;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: normal;
}

.contact__form {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 400px;
  padding-top: 25px;
  gap: 16px;
}

.contact__form-input {
  width: 100%;
  max-width: 400px;
  padding: 13px 16px;
  font-size: 16px;
  line-height: normal;
  border: none;
  border-radius: 8px;
}

.contact__form-input::placeholder {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: normal;
  color: #050d2a;
  text-transform: capitalize;
  opacity: 0.4;
}

.contact__form-textarea {
  width: 100%;
  max-width: 400px;
  min-height: 128px;
  padding: 12px 16px;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: normal;
  border-radius: 8px;
}

.contact__form-textarea::placeholder {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: normal;
  color: #050d2a;
  text-transform: capitalize;
  opacity: 0.4;
}

.contact__form-button {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 124px;
  margin: 0;
  padding: 9px 39px 8px 38px;
  font-size: 16px;
  line-height: normal;
  text-align: center;
  border-radius: 8px;
}

.contact__button-bonus {
  background: transparent;
  cursor: pointer;
  border: none;
  margin: 0 auto;
  padding: 0;
}
@media (min-width: 1200px) {
  .contact__button-bonus {
    margin: 0;
  }
}
.contact__button-bonus:hover {
  opacity: 0.7;
}
.contact__button-bonus:active {
  opacity: 0.5;
}

.footer {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 24px 20px;
  background-color: #01011f;
}
@media (min-width: 1200px) {
  .footer {
    padding: 24px 200px;
  }
}

.footer__info-list {
  display: flex;
  max-width: 500px;
  margin: 0 auto;
  margin-bottom: 16px;
  padding: 0;
  list-style: none;
}

.footer__info-item {
  position: relative;
}
.footer__info-item:not(:last-child) {
  padding-right: 20px;
}
.footer__info-item:not(:last-child)::before {
  content: "";
  position: absolute;
  top: 4px;
  right: 10px;
  width: 2px;
  height: 15px;
  background-color: #ffffff;
}

.footer__info-link {
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: normal;
  color: #ffffff;
  text-decoration: none;
}
.footer__info-link:hover {
  opacity: 0.7;
}

.footer__description {
  position: relative;
  width: 100%;
  max-width: 1040px;
  margin: 0;
  padding: 0;
  padding-bottom: 24px;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: normal;
  text-align: center;
}
.footer__description::before {
  content: "";
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 1px;
  background-color: #ffffff;
  opacity: 0.1;
}

.footer__copyright-wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  padding-top: 24px;
}

.footer__copyright-text {
  margin: 0;
  padding: 0;
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: normal;
}

.footer__social-list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  list-style: none;
  gap: 12px;
  margin: 0;
  padding: 0;
}

.footer__social-link {
  text-decoration: none;
  display: flex;
}
.footer__social-link:hover {
  opacity: 0.7;
}
.footer__social-link--twitter {
  width: 16px;
  height: 16px;
  background-image: url("../img/svg-image/twitter.svg");
  background-repeat: no-repeat;
}
.footer__social-link--facebook {
  width: 10px;
  height: 16px;
  background-image: url("../img/svg-image/facebook.svg");
}
.footer__social-link--instagram {
  width: 14px;
  height: 16px;
  background-image: url("../img/svg-image/instagram.svg");
}

.modal-container {
  overflow: hidden;
  color: #000000;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  background: rgba(242, 242, 242, 0.8);
  z-index: 2;
}
.modal-container::-webkit-scrollbar {
  display: none;
}

.modal {
  position: relative;
  margin: auto;
  padding: 64px 70px;
  background-color: #ffffff;
  border-radius: 30px;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.15);
}

.modal-form {
  width: 100%;
}

.modal-search {
  width: 717px;
  max-height: 100%;
  overflow-y: scroll;
}

.modal__content {
  display: flex;
  flex-wrap: wrap;
}

.modal__title {
  text-align: start;
  margin-bottom: 64px;
  color: #000000;
}

.modal__close-button {
  position: absolute;
  padding: 0;
  top: 64px;
  right: 52px;
  width: 52px;
  height: 52px;
  background-color: #f2f2f2;
  border: none;
  cursor: pointer;
  border-radius: 50%;
  background-image: url("../img/svg-image/close-xmark.png");
  background-repeat: no-repeat;
  background-position: center;
}

.modal__close-button:hover {
  background-color: #e6e6e6;
}

.modal__close-button:focus {
  background-color: #e6e6e6;
  outline: 3px solid #83b3d3;
}

.modal__close-button:active {
  background-color: #e6e6e6;
  outline: none;
  opacity: 0.3;
}

/* modal-form-date */
.modal-form__input {
  position: relative;
  width: 440px;
  padding: 12px 40px 12px 20px;
  background-color: #f2f2f2;
  border: none;
  border-radius: 4px;
  font-family: inherit;
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
}

.modal-form__input::placeholder {
  color: #000000;
  opacity: 0.6;
}

.modal-form__input:hover {
  background-color: #e6e6e6;
}

.modal-form__input:focus {
  background-color: #e6e6e6;
  outline: 3px solid #83b3d3;
}

.modal-form__input:not(:-moz-placeholder-shown) {
  outline: 2px solid #000000;
  background-color: #f2f2f2;
}

.modal-form__input:not(:-ms-input-placeholder) {
  outline: 2px solid #000000;
  background-color: #f2f2f2;
}

.modal-form__input:not(:placeholder-shown) {
  outline: 2px solid #000000;
  background-color: #f2f2f2;
}

.modal-form__input:active {
  opacity: 0.6;
}

.modal-form__input:disabled {
  opacity: 0.2;
}

.modal-field-container {
  position: relative;
  margin-bottom: 48px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.modal-form__description {
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  text-transform: capitalize;
}

.modal-form__label {
  max-width: 135px;
}

/* modal-button */
.modal__button-search {
  width: 100%;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  border-radius: 10px;
  padding: 18px 10px;
  margin-top: 48px;
}

.modal__button-search:active {
  background-color: #68a2ca;
}

/* modal-container-close */
.modal-container-close {
  display: none;
}

.loader-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.loader {
  border: 8px solid #f3f3f3;
  border-top: 8px solid #3498db;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}